@import "../../global.scss";
.contact {
  background-color: rgb(236, 236, 243);
  display: flex;
  @include mobile {
    flex-direction: column;
  }
  .left {
    flex: 1;
    img {
      height: 100%;
    }
  }
  .right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    h2 {
      font-size: 30px;
    }
    form {
      height: 70%;
      width: 70%;
      align-items: center;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      input {
        width: 300px;
        height: 30px;
        font-size: 14px;
        @include mobile {
          width: 200px;
          height: 20px;
        }
      }
      textarea {
        width: 300px;
        height: 200px;
        font-size: 14px;
        @include mobile {
          width: 200px;
          height: 100px;
        }
      }
      button {
        width: 150px;
        height: 30px;
        background-color: blue;
        color: white;
        border-radius: 10px;
        border: none;
        font-size: 25px;
        font-weight: 500;
        cursor: pointer;
        &:focus {
          outline: none;
        }
      }
      span {
        color: green;
      }
    }
  }
}
