@import "../../global.scss";
.intro {
  background-color: white;
  display: flex;
  @include mobile {
    flex-direction: column;
    align-items: center;
  }
  .left {
    flex: 0.5;
    overflow: hidden;
    .imgcontainer {
      width: 700px;
      height: 700px;
      background-color: rgb(220, 20, 187);
      border-radius: 50%;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      float: right;
      @include mobile {
        align-items: flex-start;
        height: 650px;
      }
      img {
        height: 90%;

        @include mobile {
          height: 60%;
        }
      }
    }
  }
  .right {
    flex: 0.5;
    position: relative;
    .wrapper {
      height: 100%;
      width: 100%;
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      @include mobile {
        padding-left: 0;
        align-items: center;
      }
      h1 {
        font-size: 60px;
        margin: 10px 0;
        @include mobile {
          font-size: 40px;
        }
      }
      h2 {
        font-size: 35px;
      }
      h3 {
        font-size: 30px;
        @include mobile {
          font-size: 20px;
        }
      }
      span {
        color: purple;
        font-size: 30px;
      }
      .ityped-cursor {
        animation: blinkcursor 1s infinite;
      }
      @keyframes blinkcursor {
        50% {
          opacity: 1;
        }
        100% {
          opacity: 0;
        }
      }
    }
    a {
      position: absolute;
      bottom: 10px;
      left: 40%;
      img {
        width: 30px;
        animation: arrowblink 2s infinite;
      }
      @keyframes arrowblink {
        100% {
          opacity: 0;
        }
      }
    }
  }
}
